import {
  Card,
  CardContent,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  TextField,
} from '@material-ui/core';
import { Edit, ScatterPlot } from '@material-ui/icons';
import React, { Component, Fragment } from 'react';

export class Flag extends Component {
  render() {
    return (
      <div>
        <h1>Flag</h1>
      </div>
    );
  }
}

export class Ingredient extends Component {
  render() {
    return (
      <Fragment>
        <h4>Ingredient</h4>
      </Fragment>
    );
  }
}

class Dish extends Component {
  state = {
    edit: false,
    name: this.props.name,
  };

  edit = (e) => {
    this.setState({ edit: !this.state.edit });
  };

  handleChange = (e) => {
    let newState = { ...this.state };
    newState.name = e.currentTarget.value;
    this.setState(newState);
    this.props.onUpdateDish(this.props.index, newState.name);
  };

  componentDidUpdate() {
    console.log('Componente actualizado');
  }

  render() {
    const { name, ingredients } = this.props;

    return (
      <Card className='card'>
        <CardContent>
          <List
            component='nav'
            subheader={
              <ListSubheader component='div'>
                {this.state.edit ? (
                  <TextField
                    label='Plato...'
                    type='text'
                    margin='normal'
                    variant='outlined'
                    value={this.state.name}
                    onChange={this.handleChange}
                  />
                ) : (
                  name
                )}
                <IconButton size='small' onClick={this.edit}>
                  <Edit />
                </IconButton>
              </ListSubheader>
            }
          >
            {ingredients.map((ingredient, index) => (
              <ListItem button key={index}>
                <ListItemIcon>
                  <ScatterPlot />
                </ListItemIcon>
                <ListItemText inset primary={ingredient} />
              </ListItem>
            ))}
          </List>
        </CardContent>
      </Card>
    );
  }
}

export default Dish;
